import React, { useEffect } from 'react';
import cover from '../../../img/cover/cover-java.webp';
import reference from '../../../img/java/reference.png';
// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';

const Types = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className={'cover-wrapper'}>
                <img src={cover} alt={'html cover'} className={'cover'} />
            </div>
            <article className={'article article-content'}>
                <h1>Referencja</h1>
                - to zmienna obiektowa, ktra wskazuje (odwouje si) do obiektu.

                <div className="article-img-wrapper">
                    <img src={reference} alt={'assignment'} className={'article-img'} />
                </div>

                <p>Moemy teraz wywoywa metody klasy <span className={'snippet'}>String</span> dla tego obiektu
                    (String "rower"), zarwno za porednictwem zmiennej <span className={'snippet'}>a</span> jak i <span
                        className={'snippet'}>b</span> - i bdzie to miao dokadnie ten sam skutek. Koniec kocw
                    metody te bd wywoywane na tym samym obiekcie klasy <span className={'snippet'}>String</span>.</p>
                <p>Zmienna obiektowa nie jest obiektem, tylko referencj do obiektu. Warto kadej zmiennej obiektowej
                    jest referencj do obiektu, ktry jest przechowywany gdzie indziej. Warto zwracana przez
                    operator <span className={'snippet'}>new</span> te jest referencj. Instrukcja typu:</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`Date deadline = new Date();`}
                    </code>
                </pre>

                <p>skada si z dwch czci. Wyraenie <span className={'snippet'}>new Date();</span> tworzy obiekt
                    typu <span className={'snippet'}>Date</span>, a jego wartoci jest referencja do tego nowo
                    utworzonego
                    obiektu.</p>
                <p>Referencja zostaje zapisana w zmiennej <span className={'snippet'}>deadline</span>. Aby zaznaczy, e
                    zmienna obiektowa nie odwouje si do adnego obiektu, trzeba jej warto ustawi na <span
                        className={'snippet'}>null</span>, bo zmienne obiektowe nie s automatycznie inicjalizowane
                    wartoci <span className={'snippet'}>null</span>.</p>

                <hr />

                <h1>String Pool</h1>
                <p>W Javie wszystkie obiekty przechowywane s w pamici w stercie pamici (<b>Memory Heap</b>). To na
                    niej dziaa Garbage Collector i jeli dostaniemy kiedy <span
                        className={'snippet'}>OutOfMemoryExcception</span> to znaczy, e zbytnio zaszalelimy z
                    tworzeniem nowych bytw i zapenilimy ca stert szybciej ni zwalnialimy miejsce. W tej stercie
                    znajduje si wydzielony obszar pamici przeznaczony do przechowywania Stringw, ktrych to w
                    programie potrafi by <u>bardzo, bardzo duo</u> - dlatego te s obsugiwane w specjalny sposb.
                    Wanie do tego suzy <b>String Pool</b>. </p>

                <p>Za kadym razem, gdy stworzymy nowego Stringa w ten sposb <span className={'snippet'}>String name = "John"</span>,
                    to w String jest tworzony w <b>String Pool</b> i tam jest przechowywany. Jaki czas pniej, przy
                    tworzeniu innego Stringa <span className={'snippet'}>String anotherName = "John"</span> nie tworzy
                    si kolejny wyraz "John" w pamici, lecz oba wskazuj na ten sam element w <b>String Pool</b>.
                    Dlatego te w tym przypadku porwanie <span className={'snippet'}>name == anotherName</span> zwrci
                    nam <span className={'snippet'}>true</span>, bo w tym wypadku obie referencje wskazuj na ten sam
                    obiekt.</p>

                <p>Sprawy maj si inaczej, gdy stworzymy Stringa w ten sposb <span className={'snippet'}>String yetAnotherName = new String("John")</span>.
                    Wwczas jest tworzony za kadym razem nowy obiekt w <b>Memory Heap</b>, a dopiero on wskazuje na
                    obiekt w <b>String Pool</b>. Dlatego porwanie <span
                        className={'snippet'}>yatAnotherName == name</span> zwrci <span
                        className={'snippet'}>false</span>, bo fizycznie s to referencje do dwch rnych obiektw.
                    Dlatego te po pierwsze nie powinno si robi nigdy <span
                        className={'snippet'}>new String("...")</span>, bo tworzy to dwa obiekty (w <b>Memory Heap</b> i
                    opcjonalnie w <b>String Pool</b>, zamiast tylko w <b>String Pool</b>). Po drugie do porwnywania
                    Stringw uywamy metody <span className={'snippet'}>.equals</span> zamiast <span
                        className={'snippet'}>==</span>, gdyby wanie kto przypadkiem stworzy tekst za pomoc <span
                        className={'snippet'}>new String</span>. Metoda <span
                        className={'snippet'}>.equals</span> porwnuje faktyczne wartoci (czyli w przypadku Stringw to
                    co jest w <b>String Pool</b>), a nie na co wskazuje referencja.</p>

                <hr />

                <h1>Immutable</h1>
                <p>W programowaniu istnieje wana koncepcja zwana <b>Immutable</b> i oznacza, e obiekt zawsze
                    przechowuje te same wartoci. Jeli musimy zmodyfikowa te wartoci, powinnimy utworzy nowy
                    obiekt. Typowym przykadem jest klasa <b>String</b>. <b>Stringi</b> s niezmiennymi obiektami, wic
                    wszystkie operacje na nich generuj nowy cig. Niezmienne typy umoliwiaj pisanie programw z
                    mniejsz liczb bdw.</p>
                <p>Klasa <span className={'snippet'}>Patient</span> nie jest niezmienna, poniewa istnieje moliwo
                    zmiany dowolnego pola obiektu.</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`Patient patient = new Patient();

patient.name = "Mary";
patient.name = "Alice";`}
                    </code>
                </pre>
            </article>
        </>
    );
};

export default Types;
