import React, { useEffect } from 'react';
import cover from '../../../img/cover/cover-java.webp';
// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';

const Generics = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className={'cover-wrapper'}>
                <img src={cover} alt={'html cover'} className={'cover'} />
            </div>
            <article className={'article article-content'}>
                <h1>Kolekcje</h1>
                <ul className={'snippet-list'}>
                    <li>wszdzie gdzie wystpuje <span>Linked</span> zachowywana jest kolejno wstawiania</li>
                    <li>dostp do pierwszego i ostatniego elementu listy zawsze odbywa si w staym czasie, O(1)
                        poniewa linki s trwale przechowywane w pierwszym i ostatnim elemencie, wic dodanie pozycji na
                        koniec listy nie oznacza, e musisz iterowa ca list w poszukiwaniu ostatniego elementu.
                        Jednak dostp do elementu bd ustawienie elementu za pomoc jego indeksu wymaga czasu O(n) w
                        przypadku <span>LinkedList</span></li>
                    <li>w oglnym przypadku <span>LinkedList</span> traci do <span>ArrayList</span> w
                        zuyciu pamici i szybkoci operacji. Ale to zaley od problemu, ktry prbujesz rozwiza.
                    </li>
                </ul>


                <hr />


                <h1>Immutable lists</h1>
                <p>Najprostszym sposobem utworzenia listy jest wywoanie
                    metody <span>of</span> interfejsu <span>List</span>. Przed wersj Java 9 innym
                    sposobem tworzenia niemodyfikowalnych list byo uycie <span>Arrays.asList(...)</span>.
                </p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`List<String> emptyList = List.of(); // 0 elements
List<String> names = List.of("Larry", "Kenny", "Sabrina"); // 3 elements
List<Integer> numbers = List.of(0, 1, 1, 2, 3, 5, 8, 13);  // 8 elements`}
                    </code>
                </pre>

                <p>Zwraca immutable list zawierajc wszystkie przekazane elementy lub pust list. Uycie tej
                    metody jest wygodne podczas tworzenia staych list lub testowania kodu. Wykonajmy kilka
                    operacji:</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`List<String> daysOfWeek = List.of(
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
        "Sunday"
);

System.out.println(daysOfWeek.size()); // 7
System.out.println(daysOfWeek.get(1)); // Tuesday
System.out.println(daysOfWeek.indexOf("Sunday")); // 6

List<String> weekDays = daysOfWeek.subList(0, 5);
System.out.println(weekDays); // [Monday, Tuesday, Wednesday, Thursday, Friday]`}
                    </code>
                </pre>

                <p>Poniewa jest immutable, bd dziaa tylko metody, ktre nie zmieniaj elementw na licie. Inne
                    rzuc wyjtek.</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`daysOfWeek.set(0, "Funday"); // throws UnsupportedOperationException
daysOfWeek.add("Holiday");   // throws UnsupportedOperationException`}
                    </code>
                </pre>

                <h1>Mutable lists</h1>
                <p>Kiedy musisz uy listy mutowalnej, moesz wzi jedn z dwch powszechnie uywanych
                    implementacji mutowalnych interfejsu <span>List</span>.
                </p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`List<Integer> numbers = new ArrayList<>();
numbers.add(15);
numbers.add(10);
numbers.add(20);

System.out.println(numbers); // [15, 10, 20]

numbers.set(0, 30); // no exceptions here

System.out.println(numbers); // [30, 10, 20]`}
                    </code>
                </pre>

                <p>Jeli masz immutable list, moesz pobra z niej mutowaln wersj, uywajc nastpujcego
                    kodu:</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`List<String> immutableList = Arrays.asList("one", "two", "three");
List<String> mutableList = new ArrayList<>(immutableList);`}
                    </code>
                </pre>

                <hr />

                <h1>Array</h1>
                <ul>
                    <li>tablica jest jednym z typw kolekcji</li>
                </ul>
                <pre className={'line-numbers language-js'}>
                    <code>
{`public class Array2D {
    public static void main(String[] args) {
        String[][] salutation = {
            //[?][0]    [?][1]    [?][2]
            {"Mr. ",   "Mrs. ",  "Ms. "},  //[0][?]
            {"Kumar",  "White",  "Rose"}   //[1][?]
        };

                           //Mr.                Kumar:
        System.out.println(salutation[0][0] + salutation[1][0]);

                           //Mrs.               White:
        System.out.println(salutation[0][1] + salutation[1][1]);

                           //Ms.                Rose:
        System.out.println(salutation[0][2] + salutation[1][2]);
    }
}`}
                    </code>
                </pre>


                <hr />


                <h1>List</h1>
                <ul className={'snippet-list'}>
                    <li>zachowuje kolejno elementw</li>
                    <li>powiksza si wraz z dodawaniem elementw</li>
                    <li>ten sam obiekt moe by przechowywany wiele razy</li>
                    <li>mona wstawi warto <span>null</span> wiele razy</li>
                    <li>limit pojemnoci liczby elementw to 32 bity (2 147 483 648), poniewa int jest
                        indeksem - metoda <span>size()</span> zwraca int.
                    </li>
                </ul>

                <b>Przykadowe implementacje:</b>
                <ul className={'snippet-list'}>
                    <li>
                        <span>java.util.ArrayList</span>
                         najczstszy wybr z racji najbardziej uniwersalnego zastosowania. Inne
                        implementacje maj przewag tylko w bardzo specyficznych przypadkach. Jeli nie
                        wiesz, jakiej listy potrzebujesz, wybierz t. Preferowana gdy czciej szukamy i
                        odwoujemy si do obiektw
                    </li>
                    <li>
                        <span>java.util.LinkedList</span>
                         szybsze dodawanie/usuwanie; moe by traktowana jako List lub Queue, poniewa
                        implementuje oba te interfejsy. LinkedList ma przewag w przypadku dodawania
                        elementw pojedynczo, w duej iloci, w sposb trudny do przewidzenia wczeniej,
                        kiedy przejmujemy si iloci zajmowanej pamici
                    </li>
                </ul>

                <b>Niektre metody:</b>
                <ul className={'snippet-list'}>
                    <li>
                        <span>add(E e)</span>
                         dodaje element na koniec listy
                    </li>
                    <li>
                        <span>add(E e, int index)</span>
                         dodaje element na wybran pozycj (nie usuwa elementu ju tam si
                        znajdujcego)
                    </li>
                    <li>
                        <span>remove(int index)</span>
                         usuwa element pod wskazanym indeksem
                    </li>
                    <li>
                        <span>remove(E e)</span>
                         usuwa pierwsze wystpienie wskazanego elementu (obiektu)
                    </li>
                    <li>
                        <span>set(E e, int index)</span>
                         wstawia element na wybran pozycj (tym samym poprzednio znajdujcy si
                        element zostaje usunity)
                    </li>
                    <li>
                        <span>get(int index)</span>
                         zwraca element o wybranym indeksie
                    </li>
                    <li>
                        <span>addAll(Collection)</span>
                         dodaje wszystkie elementy z jednej kolekcji do drugiej i wstawia je na koniec
                    </li>
                    <li>
                        <span>contains(E e)</span>
                         zwraca <span>true</span> jeeli lista zawiera dany element
                    </li>
                    <li>
                        <span>isEmpty()</span>
                         zwraca <span>true</span> jeli lista jest pusta
                    </li>
                    <li>
                        <span>size()</span>
                         zwraca aktualny rozmiar listy
                    </li>
                    <li>
                        <span>indexOf(E e)</span>
                         wyszukuje element na licie i zwraca pierwszy indeks wystpienia
                    </li>
                    <li>
                        <span>lastIndexOf(E e)</span>
                         j.w. ale zwraca ostatnie wystpienie
                    </li>
                </ul>

                <b>Przykad:</b>

                <pre className={'line-numbers language-js'}>
                    <code>
{`List<String> komputery = new LinkedList<>();

komputery.add("ZX Spectrum");
komputery.add("Commodore 64");

List<String> ulubione = new LinkedList<>();
ulubione.add("Commodore 64");
ulubione.addAll(komputery);

System.out.println(ulubione.get(1));
System.out.println(ulubione.indexOf("Commodore 64"));`}
                    </code>
                </pre>

                <hr />

                <h1>Set</h1>
                <ul className={'snippet-list'}>
                    <li>kolejno nie ma znaczenia (nie pozwala na bezporedni dostp do obiektu
                        poprzez podanie np. indeksu)
                    </li>
                    <li>przechowuje elementy unikalne - ten sam obiekt moe znajdowa si w zbiorze
                        tylko raz (nie pozwala przechowywa duplikatw)
                    </li>
                    <li>mona wstawi warto <span>null</span> jeden raz</li>
                    <li>element modszy nadpisuje element starszy</li>
                    <li>aby dosta si do obiektu musimy skorzysta z
                        ptli <span>foreach</span> lub specjalnego
                        typu <span>Iterator</span></li>
                </ul>

                <b>Przykadowe implementacje:</b>
                <ul className={'snippet-list'}>
                    <li>
                        <span>java.util.HashSet</span>
                         najczciej wystpujca  dobra wydajnociowo; brak zachowania kolejnoci
                    </li>
                    <li>
                        <span>java.util.TreeSet</span>
                         umieszcza nowe elementy kolekcji poprzez
                        uycie <span>Comparatora</span>; wszystkie elementy od razu
                        posortowane i zachowuj swoj kolejno. Implementacja oparta o drzewa
                        czerwono czarne, ktra oprcz unikalnoci elementw gwarantuje nam
                        uporzdkowanie wstawianych elementw zgodnie z naturalnym porzdkiem.
                        Porzdek ten wyznaczany jest poprzez implementacj
                        interfejsu <span>Comparable</span> lub zastosowanie
                        odpowiedniego <span>Comparatora</span> (oznacza to, e elementy w
                        zbiorze musz implementowa interfejs <span>Comparable</span> lub
                        trzeba dostarczy <span>Comparator</span>.
                    </li>
                    <li>
                        <span>java.util.LinkedHashSet</span>
                         zachowuje kolejno wpisywanych elementw (jak w Licie)
                    </li>
                </ul>

                <b>Niektre metody:</b>
                <ul className={'snippet-list'}>
                    <li>
                        <span>add(E e)</span>
                         dodawanie elementu
                    </li>
                    <li>
                        <span>addAll(Collection)</span>
                         dodaje wszystkie elementy z jednej kolekcji do drugiej
                    </li>
                    <li>
                        <span>remove(E e)</span>
                         usuwa wybrany element po wartoci, poniewa <span>Set</span> nie ma
                        indeksw
                    </li>
                    <li>
                        <span>isEmpty()</span>
                         zwraca <span>true</span> jeli zbir jest pusty
                    </li>
                    <li>
                        <span>size()</span>
                         zwraca aktualny rozmiar zbioru
                    </li>
                    <li>
                        <span>iterator()</span>
                         zwraca obiekt typu <span>Iterator</span> umoliwiajcy iterowanie
                        zbioru
                    </li>
                    <li>Ze wzgldu na brak zachowania kolejnoci zbir nie posiada metod takich
                        jak <span>get</span>, <span>indexOf</span>, <span>lastIndexOf</span>.
                        Zamiast tego posiada metody:
                    </li>
                    <ul>
                        <li>
                            <span>contains(E e)</span>
                             zwraca <span>true</span> jeli element znajduje si w zbiorze
                        </li>
                        <li>
                            <span>containsAll</span>
                             sprawdza czy zbir zawiera wszystkie elementy zbioru
                        </li>
                    </ul>
                </ul>


                <b>Przykad <span>HashSet</span>:</b>

                <pre className={'line-numbers language-js'}>
                    <code>
{`Set<String> komputery = new HashSet<>();

komputery.add("ZX Spectrum");
komputery.add("Commodore 64");
komputery.add("Commodore 64");
komputery.add("Amiga 500");

System.out.println(komputery.size());
System.out.println(komputery.contains("Commodore 64"));
komputery.remove("Commodore 64");
System.out.println(komputery.contains("Commodore 64"));`}
                    </code>
                </pre>

                <b>Przykad <span>TreeSet</span>:</b>
                <pre className={'line-numbers language-js'}>
                    <code>
{`//Tworzymy zmienn o typie interfejsu i inicjalizujemy:
Set<String> imiona = new TreeSet<>();

imiona.add("Zosia");
imiona.add("Krysia");
imiona.add("Albert");
imiona.add("Krysia");

for (String imie: imiona) {
    System.out.println(imie);
}`}
                    </code>
                </pre>


                <hr />


                <h1>Map</h1>
                <ul>
                    <li>mapy nie implementuj interfejsu Collection</li>
                    <li>przechowuje pary klucz : warto</li>
                    <li>w odrnieniu od indeksu w licie (gdzie wymaga si liczb cakowitych),
                        kluczem moe by inny obiekt niemodykowalny
                    </li>
                    <li>klucz musi by unikalny</li>
                    <li>wartoci mog si powtarza</li>
                </ul>

                <b>Przykadowe implementacje:</b>
                <ul className={'snippet-list'}>
                    <li>
                        <span>java.util.HashMap</span>
                         najczciej wystpujca  dobra wydajnociowo; brak zachowania
                        kolejnoci
                    </li>
                    <li>
                        <span>java.util.LinkedHashMap</span>
                         zachowuje kolejno wpisywanych elementw (jak w Licie)
                    </li>
                    <li>
                        <span>java.util.TreeMap</span>
                         umieszcza nowe elementy kolekcji poprzez
                        uycie <span>Comparatora</span>; wszystkie elementy od razu
                        posortowane wzgldem klucza i zachowuj swoj kolejno
                    </li>
                </ul>

                <b>Niektre metody:</b>
                <ul className={'snippet-list'}>
                    <li>
                        <span>put(Key key, Value value)</span>
                         umieszcza now par klucz : warto w mapie(lub nadpisanie, gdy klucz
                        istnieje!)
                    </li>
                    <li>
                        <span>putAll</span>
                         dodaje wszystkie elementy z jednej mapy do drugiej
                    </li>
                    <li>
                        <span>containsKey(Object o)</span>
                         zwraca <span>true</span> jeli mapa zawiera wskazany klucz
                    </li>
                    <li>
                        <span>containsValue(Object o)</span>
                         zwraca <span>true</span> jeli mapa zawiera wskazan warto
                    </li>
                    <li>
                        <span>isEmpty()</span>
                         zwraca <span>true</span> jeli mapa jest pusta
                    </li>
                    <li>
                        <span>size()</span>
                         zwraca aktualny rozmiar mapy
                    </li>
                    <li>
                        <span>remove(Object key)</span>
                         usuwa wybran par klucz : warto na podstawie klucza
                    </li>
                    <li>
                        <span>get(Object key)</span>
                         zwraca warto przypisan dla wskazanego klucza
                    </li>
                    <li>
                        <span>keySet()</span>
                         zwraca set wszystkich kluczy mapie
                    </li>
                    <li>
                        <span>replace(Key key, Value value)</span>
                         podmienia warto dla wskazanego klucza
                    </li>
                </ul>

                <b>Przykad <span>HashMap</span> z wykorzystaniem
                    ptli <span>foreach</span> do iterowania:</b>

                <pre className={'line-numbers language-js'}>
                    <code>
{`Map<String, String> procesory = new HashMap<>();

procesory.put("IBM PC", "Intel x86");
procesory.put("Atari ST", "Motorola 68000");
procesory.put("Commodore 64", "MOS 6502");
procesory.put("Amiga 500", "Motorola 68000");
procesory.put("IBM PC", "Intel x86-64");

System.out.println(procesory.size());
System.out.println(procesory.get("Amiga 500"));
System.out.println(procesory.get("IBM PC"));

//iterowanie po wartociach:
for (String wartosc : procesory.values()) {
    System.out.println(wartosc);
}

//iterowanie po kluczach:
for (String klucz : procesory.keySet()) {
    System.out.println(klucz);
    System.out.println(procesory.get(klucz));
}

//iterowanie po parach:
for (java.util.Map.Entry para : procesory.entrySet()) {
    System.out.println(para.getKey());
    System.out.println(para.getValue());
}`}
                    </code>
                </pre>

                <hr />

                <h1>Queue</h1>
                <ul>
                    <li>pozwalajca na implementacj kolejek typu FIFO (first-in-first-out)
                        i FILO (first-in-last-out)
                    </li>
                </ul>

                <b>Przykadowe implementacje:</b>
                <ul className={'snippet-list'}>
                    <li>
                        <span>java.util.ArrayDeque</span>
                         kolejka oparta o tablice, pozwala na dostp zarwno od strony
                        gowy <span>getFirst()</span> jak i
                        ogona <span>getLast()</span>. Elementy przechowywane s w
                        kolejnoci dodawania
                    </li>
                    <li>
                        <span>java.util.PriorityQueue</span>
                         pozwala na przechowywanie i dostp do elementw wg okrelonego
                        kryterium (komparatora), sortujc elementy wg niego w momencie
                        dodania do kolejki. Przydatna w sytuacjach gdy mamy np. kolejk
                        obiektw do przetworzenia i chcemy zawsze obsuy waniejsze
                        szybciej ni te mniej wane
                    </li>
                </ul>

                <b>Niektre metody:</b>
                <ul className={'snippet-list'}>
                    <li>
                        <span>boolean offer(E e)</span>
                        wstawia okrelony element do kolejki, jeli jest to moliwe od razu
                        bez naruszania ogranicze pojemnoci;
                        zwraca <span>true</span> / <span>false</span> w
                        zalenoci od wyniku tej operacji
                    </li>
                    <li>
                        <span>E remove()</span>
                        pobiera i usuwa head tej kolejki; jeli jest pusty, metoda
                        rzuca <span>NoSuchElementException</span></li>
                    <li>
                        <span>E poll()</span>
                        pobiera i usuwa head tej kolejki lub
                        zwraca <span>null</span> jeli ta kolejka jest pusta
                    </li>
                    <li>
                        <span>E element()</span>
                        pobiera, ale nie usuwa head kolejki; jeli jest pusty, metoda
                        rzuca <span>NoSuchElementException</span></li>
                    <li>
                        <span>E peek()</span>
                        pobiera, ale nie usuwa head tej kolejki lub
                        zwraca <span>null</span> jeli ta kolejka jest pusta
                    </li>
                    <li>
                        <span>add(E element)</span>
                        metoda dziaa tak samo jak <span>offer(E e)</span> ale
                        rzuca <span>IllegalStateException</span> jeli nie jest
                        obecnie dostpna przestrze;
                    </li>
                </ul>

                <b>Przykad</b>
                <pre className={'line-numbers language-js'}>
                    <code>
{`Queue<String> kolejka = new ArrayDeque<String>();
kolejka.add("pierwszy");
kolejka.add("drugi");

System.out.println(kolejka.remove()); //wypisze "pierwszy"`}
                    </code>
                </pre>
            </article>
        </>
    );
};

export default Generics;
