import React, { useEffect } from 'react';
import cover from '../../../img/cover/cover-java.webp';
// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';

const Generics = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className={'cover-wrapper'}>
                <img src={cover} alt={'html cover'} className={'cover'} />
            </div>
            <article className={'article article-content'}>
                <h1>Typy generyczne</h1>
                <p>Istniej sytuacje, w ktrych metody i klasy nie zale od typw danych, na ktrych dziaaj. Na
                    przykad algorytm znajdujcy element w tablicy - moe przetwarza tablice stringw, intw lub
                    niestandardowych klas. Nie ma znaczenia, co przechowuje tablica: algorytm jest zawsze taki sam.
                    Jednak nie moemy napisa go jako jednej metody, poniewa wymaga rnych argumentw (
                    <span className={'snippet'}>int[]</span>, <span className={'snippet'}>String[]</span>, itp).
                    Od wersji 5 Java obsuguje programowanie generyczne, ktre wprowadza abstrakcje nad typami. Typy
                    sparametryzowane pozwalaj nam zadeklarowa metod lub klas, ktra obsuguje rne typy w ten sam
                    oglny sposb. Konkretny typ jest okrelany tylko wtedy, gdy programista tworzy obiekt klasy lub
                    wywouje metod. Takie podejcie umoliwia nam pisanie bardziej abstrakcyjnego kodu i tworzenie
                    bibliotek oprogramowania wielokrotnego uytku.
                </p>

                <p>Typy generyczne to co w rodzaju "szablonw". Pozwalaj tworzy klasy, ktre dopasowuj si do
                    kontekstu. Pozwala na pisanie kodu programu bez wczeniejszej znajomoci typw danych, na ktrych
                    ten kod bdzie pracowa. Typ danych podaje si dopiero w momencie uycia. Typy generyczne pozwalaj
                    na unikanie rzutowania.</p>

                <p>Konwencja nazewnicza typu danych:</p>
                <ul className={'snippet-list'}>
                    <li>
                        <span>T</span>
                         Type
                    </li>
                    <li>
                        <span>S</span>
                        , <span>U</span>, <span>V</span>  kolejne n-te typy
                    </li>
                    <li>
                        <span>E</span>
                         Element (uywany szeroko w rnych kolekcjach)
                    </li>
                    <li>
                        <span>K</span>
                         Key
                    </li>
                    <li>
                        <span>V</span>
                         Value
                    </li>
                    <li>
                        <span>N</span>
                         Number
                    </li>
                </ul>


                <hr />

                <h1>Deklaracja klacy generycznej</h1>
                <p>Aby zadeklarowa klas generyczn, powinnimy zadeklarowa klas z sekcj parametru typu z
                    nawiasami ostrymi po nazwie klasy. W poniszym przykadzie
                    klasa <span className={'snippet'}>GenericType</span> ma pojedynczy parametr typu o nazwie <span
                        className={'snippet'}>T</span>.
                    Zakadamy, e typ <span className={'snippet'}>T</span> jest jakim typem i piszemy tre klasy
                    niezalenie od
                    konkretnego typu.
                </p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`class GenericType<T> {
    /**
     * A field of "some type"
     */
    private T t;

    /**
     * Takes a value of "some type" and set it to the field
     */
    public GenericType(T t) {
        this.t = t;
    }

    /**
     * Returns a value of "some type"
     */
    public T get() {
        return t;
    }

    /**
     * Takes a value of "some type", assigns it to a field and then returns it
     */
    public T set(T t) {
        this.t = t;
    return this.t;
    }
}`}
                    </code>
                </pre>

                <p>Aby utworzy obiekt klasy oglnej (standardowej lub niestandardowej), powinnimy okreli
                    argument typu po nazwie typu.</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`GenericType<Integer> obj1 = new GenericType<Integer>(10);
GenericType<String> obj2 = new GenericType<String>("abc");`}
                    </code>
                </pre>

                <p>Wane jest, aby argument typu by typem referencyjnym. Nie mona uy typu pierwotnego, takiego
                    jak <span className={'snippet'}>int</span> lub <span className={'snippet'}>double</span> jako
                    argumentu typu. Od wersji Java 7
                    moliwe byo zastpienie argumentw typu wymaganych do wywoania konstruktora klasy oglnej
                    pustym zestawem argumentw typu, o ile kompilator moe wywnioskowa argumenty typu z kontekstu.
                </p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`GenericType<Integer> obj1 = new GenericType<>(10);
GenericType<String> obj2 = new GenericType<>("abc");`}
                    </code>
                </pre>

                <p>Para nawiasw ostrych <span className={'snippet'}>&lt;&gt;</span> jest nieformalnie nazywana
                    operatorem
                    diamentowym. Czasami zadeklarowanie zmiennej za pomoc typu oglnego moe by zbyt dugie i
                    sabo czytelne. Na szczcie moesz napisa <span className={'snippet'}>var</span> zamiast
                    okrelonego typu, aby
                    wymusi automatyczne wnioskowanie o typie na podstawie typu przypisanej wartoci.</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`var obj3 = new GenericType<>("abc");`}
                    </code>
                </pre>

                <p>Po utworzeniu obiektu z okrelonym argumentem typu moemy wywoa metody klasy, ktre
                    pobieraj lub zwracaj parametr typu:</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`Integer number = obj1.get(); // 10
String string = obj2.get();    // "abc"

System.out.println(obj1.set(20));    // prints the number 20
System.out.println(obj2.set("def")); // prints the string "def"`}
                    </code>
                </pre>


                <hr />


                <h1>Przykad</h1>
                <p>Na przykad wyobramy sobie, e chcemy tworzy pokrowce na telefony:</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`public class SamsunGalaksyS9 { }

public class FuteralNaSamsunga {
    private SamsunGalaksyS9 telefon;
    public FuteralNaSamsunga(SamsunGalaksyS9 tel) {
        this.telefon = tel;
    }
}`}
                    </code>
                </pre>

                <p>Mona wic stworzy typ generyczny:</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`public class Nokya { }

public class ApelX { }

public class FuteralUniwersalny<T> {
    private T telefon;
    public FuteralUniwersalny(T tel) {
        this.telefon = tel;
    }
}`}
                    </code>
                </pre>

                <p>Uycie:</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`FuteralUniwersalny<Nokya> nokyaFuteral = new FuteralUniwersalny<Nokya>(new Nokya());
FuteralUniwersalny<ApelX> apelFuteral = new FuteralUniwersalny<ApelX>(new ApelX());`}
                    </code>
                </pre>

                <hr />

                <h1>Object a typ generyczny</h1>

                <p>Jeli zadeklarujemy pole klasy jako <span className={'snippet'}>Object</span>, moemy
                    przypisa do
                    niego warto dowolnego typu odwoania. To podejcie byo szeroko stosowane przed
                    wersj Java 5.
                </p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`class NonGenericClass {
    private Object val;
    public NonGenericClass(Object val) {
        this.val = val;
    }

    public Object get() {
        return val;
    }
}`}
                    </code>
                </pre>

                <p>Moliwe jest utworzenie instancji przekazujcej warto
                    typu <span className={'snippet'}>Integer</span> lub <span
                        className={'snippet'}>Character</span>. W ten sposb
                    moesz ponownie uy tej samej klasy z polem <span
                        className={'snippet'}>Object</span> do
                    przechowywania rnych typw w ten sam sposb. Po wywoaniu
                    metody <span className={'snippet'}>get()</span> otrzymujemy <span
                        className={'snippet'}>Object</span>, a nie
                    a <span className={'snippet'}>String</span> lub <span
                        className={'snippet'}>Integer</span>. Nie moemy pobra
                    stringa bezporednio z metody. Aby odzyska cig znakw, powinnimy wykona jawne
                    rzutowanie typu na klas <span className={'snippet'}>String</span>.
                </p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`NonGenericClass instance2 = new NonGenericClass("abc");
String str = instance2.get(); // Compile-time error: Incompatible types
String str = (String) instance2.get(); // "abc"`}
                    </code>
                </pre>

                <p>Oczywicie to zadziaa, ale co, jeli instancja w ogle nie przechowuje stringa?
                    Poniewa jest zadeklarowana jako <span className={'snippet'}>Object</span>, warto
                    pola, moe
                    zachowa dowolny typ. W takim przypadku kod zgasza wyjtek. Oto przykad:</p>

                <pre className={'line-numbers language-js'}>
                    <code>
{`NonGenericClass instance3 = new NonGenericClass(123);
String str = (String) instance3.get(); // throws java.lang.ClassCastException`}
                    </code>
                </pre>

                <p>Teraz moemy zobaczy gwn przewag typw generycznych nad
                    klas <span className={'snippet'}>Object</span> do ponownego wykorzystania kodu. Nie
                    ma potrzeby
                    wykonywania jawnego rzutowania typu, w zwizku z czym nigdy nie otrzymujemy
                    wyjtku runtime. Jeli zrobimy co nieprawidowo, moemy to zobaczy w czasie
                    kompilacji.
                </p>


                <pre className={'line-numbers language-js'}>
                    <code>
{`GenericType<String> instance4 = new GenericType<>("abc");

String str = instance4.get(); // There is no type-casting here
Integer num = instance4.get(); // It does not compile`}
                    </code>
                </pre>

                <p>Bd kompilacji jest wykrywany przez programist, a nie uytkownika programu. To
                    sprawia, e typy generyczne s zarwno elastyczne, jak i bezpieczne.
                    Jednoczenie praca z <span className={'snippet'}>Object</span>, ktry wymaga
                    rzutowania typw
                    jest podatna na bdy. Lepiej pozwoli, aby zaj si tym kompilator.
                </p>
            </article>
        </>
    );
};

export default Generics;
