import React, { useEffect } from 'react';
import cover from '../../../img/cover/cover-javascript.webp';
import eventCodeEventKey from '../../../img/javascript/event-code-event-key.png';
// @ts-ignore
import Prism from 'prismjs';
import 'prismjs/themes/prism-tomorrow.css';
import 'prismjs/plugins/line-numbers/prism-line-numbers.js';
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';

const EventListener = () => {
    useEffect(() => Prism.highlightAll(), []);

    return (
        <>
            <div className={'cover-wrapper'}>
                <img src={cover} alt={'html cover'} className={'cover'} />
            </div>
            <article className={'article article-content'}>
                <h1>keydown</h1>
                <pre className={'line-numbers language-js'}>
                    <code>
{`document.addEventListener("keydown", function(event) {
    if (event.code == "AltRight") {
        console.log(event);
    }
});`}
                    </code>
                </pre>

                <p>W tym kodzie napisalimy procedur obsugi zdarzenia <span className={'snippet'}>keydown</span>,
                    ktre powinno
                    nastpi po naciniciu prawego klawisza <span className={'keyboard'}>Alt</span>. Aby nasz program
                    obsugi zdarze
                    dziaa poprawnie, parametr <span className={'snippet'}>event</span> zosta przekazany do funkcji
                    (czasami jest po
                    prostu oznaczony liter <b>e</b>). Ten parametr jest odniesieniem do obiektu globalnego,
                    przegldarka musi umieci w nim wszystkie dane biecego zdarzenia. Rezultatem wykonania tych
                    wierszy kodu bdzie wyjcie do konsoli informacji, ktre zostay przekazane
                    do <span className={'snippet'}>event</span>.
                </p>
                <p>JS ma waciwo <span className={'snippet'}>event.code</span>, ktra pozwala rozpozna wcinity
                    klawisz. Uywaj:
                </p>
                <ul className={'snippet-list'}>
                    <li>
                        <span>event.code</span>, gdy <b>nie</b> zaley ci na wielkoci liter
                    </li>
                    <li>
                        <span>event.key</span> , gdy zaley ci na wielkoci liter. <span>event.key</span> jest rwnie
                        przydatny, jeli
                        twoja aplikacja jest uywana przez wielojzycznych uytkownikw, poniewa w przypadku niektrych
                        ukadw <span>event.code</span> moe wystpi nieoczekiwany znak. Wicej informacji na ten
                        temat mona znale w witrynie <a href="https://www.w3.org/TR/uievents-code/#table-key-code-alphanumeric-writing-system">World
                        Wide Web Consortium</a>.
                    </li>
                </ul>

                <div className={'article-img-wrapper'}>
                    <img src={eventCodeEventKey} alt={'event code, event key'} className={'article-img'} />
                </div>
            </article>
        </>
    );
};

export default EventListener;
